import { App } from 'vue'
import NdButton from './button'
import Share from "./Share";

// 
const components = [ NdButton,Share ]

//  install  App 
const install = (app: App): void => {
    // 
    components.map((component) => app.component(component.name, component))
}

export {
    NdButton,Share
}

export default {
    install
}
